
















































import { Component, Vue } from 'vue-property-decorator';
import Submit from '../components/Submit.vue';

@Component({
  name: 'Home',
  components: {
    Submit,
  },
})
export default class Profile extends Vue {
  phone = '';

  name = '';

  company = '';

  submiting = false;

  validatorPhone(val: string) {
    console.log(234234);
    return /^(?:(?:\+|00)86)?1\d{10}$/.test(val);
  }

  validatorCaptcha(val: string) {
    return /^\d{1,}$/.test(val);
  }

  async onSubmit() {
    try {
      this.submiting = true;
      await this.$xhr.api.post('/users', {
        params: {
          phone: this.phone,
          name: this.name,
          company: this.company,
        },
      });
      this.submiting = false;
      this.$router.replace({ name: 'result', query: { phone: this.phone } });
    } catch (error) {
      this.submiting = false;
      // eslint-disable-next-line
      // @ts-ignore
      this.$toast(error && error.message || '网络不给力');
    }
  }

  onFormFailed(errorInfo: any) {
    const { errors } = errorInfo;
    const validFied = errors[0];
    this.$toast(validFied.message);
  }

  mounted() {
    if (this.$route.query.phone) {
      this.phone = `${this.$route.query.phone}`;
    } else {
      this.$router.replace({ name: 'login' });
    }
  }
}
